body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.search-text {
  font-size: 20px;
  font-weight: bold;
  margin: 20px 0;
}

.search-bar {
  font-size: 18px;
  padding: 0 2px;
}

.search-submit {
  font-size: 18px;
}

.results-content {
  margin-top: 20px;
}

.accent {
  color: darkred;
  font-weight: 500;
}

.results-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.card-content {
  padding: 0 3%;
  min-width: 20%;
  border: 1px solid gray;
  border-radius: 2%;
  margin: 2%;
}

.card-content:hover {
  box-shadow: 10px 10px 6px -6px #777;
}

.margin-img {
  margin-top: 5%;
}
